.comment-list {
  list-style: none;
  padding: 0;
  margin-top: 30px;
  border-top: 1px solid #b3aaa1;

  &__item {
    padding: 5px 10px;
    margin-top: 10px;
    background-color: #fdfcfb;
    border-radius: 5px;
    text-transform: capitalize;
    display: flex;
    flex-direction: column;
  }
}
